.App {
    text-align: center;
}

#wrapper {
    background-image: linear-gradient(to top, rgba(255,0,0,0), rgba(147, 179, 217, 0.8));
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

#quote-box {
    border-radius: 25px !important;
    border: 1px solid white;
    width: 500px;
    min-height: 300px;
    opacity: 0.98;
}
#author {
    float: right;
}

#text {
    padding: 20px;
}

.fade-out {
    transition:opacity ease-in-out;
    transition-duration: 500ms;
    opacity:0.2;
}
.fade-in {
    transition:opacity ease-in-out;
    transition-duration: 500ms;
    opacity:1;
}

#new-quote {
    float: right
}

#tweet-quote {
    float: left
}

#bottom-bar {
    bottom: 20px;
    padding: 10px;
}